<template>
  <div>
    <h4>
      <font style="font-size:30px">{{num}}</font>
    </h4>
    <button @click="goHome">跳过</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      num: 5,
      cleardata: null //清除定时器
    };
  },
  methods: {
    goHome() {
      this.$router.push({ name: "Home" });
    }
  },
  created() {
    // 倒计时定时器
    this.cleardata = setInterval(() => {
      if (this.num == 0) {
        this.$router.push({ name: "Home" });
      } else {
        this.num--;
      }
    }, 1000);
    this.$store.state.vanTabbar = false;
  },
  // 销毁生命周期函数之前停止
  beforeDestroy() {
    //销毁vue数据与方法。组件结束执行
    // 定时器属于浏览器对象，属于window。
    clearInterval(this.cleardata);
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    this.$store.state.vanTabbar = true;
  }
};
</script>

<style lang="less" scoped>
div {
  position: fixed;
  height: 100%;
  width: 100%;
  background: url(../assets/y5.jpg) no-repeat;
  background-size: cover;
}
button {
  width: 60px;
  height: 30px;
  background: none;
  border: 1px solid #aaa;
  font-size: 16px;
  color: rgb(146, 156, 156);
  margin-left: 10px;
}
font {
  padding: 10px;
}
</style>